.message{
    display:flex;
    flex-direction:column;
    margin-top:20px;

    .messageTop{
        display:flex;
        .messageImg{
            width:40px;
            height:40px;
            border-radius:50%;
            object-fit:cover;
            margin-right:10px;
        }
        .messageText{
            padding:10px;
            border-radius:20px;
            background-color:rgb(62, 116, 233);
            color:white;
            max-width:300px;
        }
    }
    .messageBottom{
        font-size:12px;
        margin-top:10px;
    }
}
.message.own{
    align-items:flex-end;
    .messageTop{
        display:flex;
    }
    .messageText{
        background-color:rgb(235, 229, 229);
        color:black;
    }
}
